class Logger {
  debugModeEnabled: boolean;

  constructor(debugModeEnabled?: boolean) {
    this.debugModeEnabled = !!debugModeEnabled
  }

  debug(...args: any) {
    if (!this.debugModeEnabled) {
      return
    }

    console.debug('[Deska]', ...args)
  }

  log(...args: any) {
    if (!this.debugModeEnabled) {
      return
    }

    console.log('[Deska]', ...args)
  }

  info(...args: any) {
    console.info('[Deska]', ...args)
  }

  warn(...args: any) {
    console.warn('[Deska]', ...args)
  }

  error(...args: any) {
    console.error('[Deska]', ...args)
  }
}

export default Logger
